.slider-wrapper {
    width: 760px;
    margin: auto;
  }
  .slick-slide {
    text-align: center;
    position: relative;
    margin: 10px;
    justify-content: space-between;
  }
  .slick-slide:focus {
    outline: none;
  }
  .slick-slide-title {
    text-transform: capitalize;
    font-size: 20px;
    color: #515355;
  }
  .slick-slide-image {
    max-width: 100%;
    height: auto;
    justify-content: center;
    border-radius: 8px;
    box-shadow: 0 13px 27px -5px hsla(240, 30.1%, 28%, 0.25), 0 8px 16px -8px hsla(0, 0%, 0%, 0.3),
      0 -6px 16px -6px hsla(0, 0%, 0%, 0.03);
  }
  .slick-slide-label {
    color: #fff;
    padding: 10px;
    position: absolute;
    left: 0px;
    font-size: 15px;
    bottom: 0px;
    width: 100%;
  }
  .slick-prev:before,
  .slick-next:before {
    color: #949697;
    font-size: 20px;
  }
